import React from "react";
import PropTypes from "prop-types";
import styles from "./transactions.module.css";

const TransactionHistory = ({items}) => (
  <table className={styles.history}>
    <thead className={styles.head}>
      <tr className={styles.headList}>
        <th className={styles.type}>Type</th>
        <th className={styles.amount}>Amount</th>
        <th className={styles.currency}>Currency</th>
      </tr>
    </thead>

    <tbody className={styles.body}>
      {items.map(item => (
        <tr className={styles.items} key={item.id}>
          <td className={styles.item}>{item.type}</td>
          <td className={styles.item}>{item.amount}</td>
          <td className={styles.item}>{item.currency}</td>
        </tr>
      ))}
    </tbody>
  </table>
);

TransactionHistory.defaultProps = {
  type: PropTypes.string.isRequired,
  amount: PropTypes.number.isRequired,
  currency: PropTypes.string.isRequired,
  id: PropTypes.number.isRequired
};

export default TransactionHistory;
